.congraluation-main{
  font-family: 'Poppins', sans-serif;
}
.circle {
  position: relative;
  margin: 20px;
  height: 300px;
  width: 300px;
  /* background-color: orange; */
  border-radius: 50%;
  margin: auto;
  display: flex;
  justify-content: center;
  align-items: center;
}

.circle:before {
  position: absolute;
  content: "";
  height: calc(100% + 10px);
  width: calc(100% + 10px);
  border: 5px dashed #04724d;
  top: -7px;
  left: -7px;
  border-radius: inherit;
  animation: spin 10s linear infinite;
  /* animation-iteration-count: 1 */
}

@keyframes spin {
  100% {
    transform: rotateZ(360deg);
  }
}

.circle2 {
  position: relative;
  margin: 20px;
  height: 250px;
  width: 250px;
  /* background-color: orange; */
  background-color: rgba(4, 114, 77, 0.13);
  border-radius: 50%;
  display: flex;
}

.mainCircle {
  margin-top: 2%;
}
.circle2:before {
  position: absolute;
  content: "";
  height: calc(100% + 10px);
  width: calc(100% + 10px);
  border: 5px dashed #04724d;
  top: -11px;
  left: -8px;
  border-radius: inherit;
  animation: spin 10s linear infinite;
  /* animation-iteration-count: 1 */
}

@keyframes spin {
  100% {
    transform: rotateZ(360deg);
  }
}

.tick-icon-div {
  position: relative;
  top: 20%;
  left: 15%;
}

.congratualtion-text  {
  letter-spacing: 0px;
  color: #333;
  opacity: 1;
  font-size: 24px;
  text-align: center;
  font-weight: 600;
  padding: 5px 0;
}
.birbalLogoCongo {
  display: flex;
  flex-direction: row-reverse;
}
.mainPage {
    display: block !important;
    justify-content: flex-start !important;
}

@media(max-width:1024px){
  .birbalLogoCongo{
    display: none;
  } 
  .mainCircle{
    margin: 20% 20px 0 20px;
    background: #fff;
    border-radius: 25px;
    padding: 20px;
   
  }

.circle{
  width: 150px;
  height: 150px;
  margin-bottom: 20px;
}
.circle2{
  width: 100px;
  height: 100px;
}
.tick-icon-div{

}
.tick-icon-div img{
  width: 60px;
}
.congratualtion-text{
  font-size: 13px;
}
}